.input {
  padding: 1rem;
  width: 90%;
  margin: 2rem auto;
  max-width: 40rem;
}

.input label {
  display: block;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

.input input {
  padding: 0.15rem;
  margin-bottom: 0.5rem;
  display: block;
  width: 100%;
  border: 1px solid #43665e;
  font: inherit;
}

.button {
  display: flex;
  justify-content: flex-end;
}

.input input:focus {
  outline: none;
  border-color: #43665e;
}
