.btn {
  padding: 0.25rem 1rem;
  font: inherit;
  border: 1px solid #384d48;
  background-color: #384d48;
  color: white;
  cursor: pointer;
  border-radius: 12px;
  font-weight: 600;
}

.btn:hover,
.btn:active {
  background-color: #43665e;
  border-color: #43665e;
}

.btn:focus {
  outline: none;
}
