.invalid-input {
  position: fixed;
  top: 30vh;
  left: 10%;
  background-color: #d8d4d5;
  width: 80%;
  z-index: 15;
}

.invalid-input header {
  background-color: #384d48;
  padding: 1rem;
}

.invalid-input h2 {
  color: white;
  margin: 0;
}

.invalid-input footer {
  padding: 1rem;
}
.content {
  padding: 1rem;
}
.invalid-input p {
  font-size: 1.5rem;
  line-height: 1.5;
}

.actions {
  display: flex;
  justify-content: flex-end;
  padding: 1rem;
}
